<template>
  <div>
    <router-link v-if="!isDeviceLinked" class="device" :to="linkRoute">
      <img class="device__icon" src="@/assets/icon-device.svg" />
      <span class="device__link-text">{{ $t("INSTALL_BRAND") }}</span>
    </router-link>
    <button v-else class="device device_linked" :class="{ device_expanded: isDropdownShown }" @click="toggleDropdown">
      <img class="device__icon" src="@/assets/icon-device.svg" />
      <span class="device__selected">{{ $t("SELECTED_DEVICE") }}</span>
      <span class="device__text">{{ currentDevice.model }} <span v-if="currentDevice.name">{{ currentDevice.name
          }}</span></span>
      <span class="device__pro" v-if="currentDevice.plus">PLUS</span>
      <span class="device__premium" v-if="currentDevice.premium">PREMIUM</span>
      <img class="device__d-icon device__d-icon_desktop" src="@/assets/icon-dropdown-white.svg" />
      <img class="device__d-icon device__d-icon_mobile" src="@/assets/icon-dropdown-black.svg" />
      <span class="device__desc">
        {{ currentDevice.ios }},
        {{ config.signing_with }}
        <template v-if="!hasPlus && !hasPremium && config && config.free_signs_left != undefined">
          {{ $t('N_FREE_SIGNS_LEFT', { n: config.free_signs_left }) }}
        </template>
      </span>

      <ul class="dropdown" v-show="isDropdownShown">
        <li class="dropdown__item" v-for="(item, index) in menuItems" :key="index">
          <a v-if="item.external" class="dropdown__link" :href="item.link" :target="item.target">
            {{ item.title }}
          </a>
          <router-link v-else class="dropdown__link" :to="item.link">
            {{ item.title }}
          </router-link>
        </li>
      </ul>
    </button>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import isItselfLinkable from '@/utils/isItselfLinkable';
import getMainMenuItems from '@/utils/MainMenu';

export default {
  data() {
    return {
      isDropdownShown: false,
      menuItems: []
    };
  },
  computed: {
    ...mapGetters(['hasPlus', 'hasPremium', 'config']),
    linkRoute() {
      return isItselfLinkable() ? '/link' : '/link-qr';
    },
    isDeviceLinked() {
      return this.$store.getters.isDeviceLinked;
    },
    currentDevice() {
      const { devices } = this.$store.getters;
      if (devices.length) {
        return devices.find(device => device.lt === this.$store.getters.lt);
      }

      const { config } = this.$store.getters;
      if (Object.keys(config).length) {
        return {
          model: config.nice_idevice_model,
          ios: config.ios_version,
          signing_with: config.signing_with,
          app_installation_method: config.app_installation_method,
        };
      }

      return null;
    },
  },
  methods: {
    toggleDropdown() {
      this.isDropdownShown = !this.isDropdownShown;
    },
    handleDropdown(e) {
      if (e.path.some(el => typeof el.className === 'string' && el.className.split(' ').includes('device'))) {
        return;
      }
      this.isDropdownShown = false;
    },
  },
  created() {
    this.menuItems = getMainMenuItems();
  },
  mounted() {
    document.body.addEventListener('click', this.handleDropdown);
  },
  destroyed() {
    document.body.removeEventListener('click', this.handleDropdown);
  },
};
</script>

