export class APIError extends Error {
  constructor(response) {
    const { code } = response.errors[0]; // showing only first code for backwards compatibility
    super(code);

    this.response = response;
    this.code = code;
    this.translatedMessage = response.errors.map(({ translated }) => translated).join('\n');
  }
}

function checkBan(response) {
  const error = response.errors.find(e => e.code === 'ERROR_DEVICE_BANNED');
  if (!response.success && error) {
    if (Vue.router.currentRoute.name !== 'ban') {
      Vue.router.push({
        name: 'ban',
        params: {
          reason: response.data,
          heading: error.translated,
        },
      });
    }
  }
}

function checkPlusRequired(response) {
  const error = response.errors.find(e => e.code === 'ERROR_PLUS_REQUIRED');
  if (!error || !response.data) {
    return;
  }

  Vue.router.push({
    path: response.data,
  });

  throw new APIError(response);
}

function checkCustomerToken(response) {
  const isTokenInvalid = response.errors.some(e => e.code === 'ERROR_BROKEN_LINK_TOKEN');
  if (isTokenInvalid) {
    Vue.store.commit('lt', '');
  }
}

function checkPublisherToken(response) {
  const isTokenInvalid = response.errors.some(e => e.code === 'ERROR_INVALID_STAFF_TOKEN');
  if (isTokenInvalid) {
    Vue.store.commit('st', '');
  }
}

