  async saveConfiguration(config) {
    const params = {};
    Object.keys(config)
      .forEach((key) => {
        params[`params[${key}]`] = config[key];
      });

    const getCodeAndSave = () => Vue.prototype.$prompt(i18n.t('ENTER_OTP'))
      .then((code) => {
        params['params[2fa_code]'] = code;
        return this.post('configure', params);
      })
      .catch((e) => {
        if (e && e.message === 'ERROR_2FA_CODE_IS_WRONG') {
          return getCodeAndSave();
        }

        throw e;
      });

    return this.post('configure', params)
      .catch((e) => {
        if (e && e.message === 'ERROR_2FA_CODE_IS_REQUIRED') {
          return getCodeAndSave();
        }

        throw e;
      });
  }

  async installApp({
    id,
    type,
    features,
    isFromRepo,
    name,
    link,
    image,
    version,
    installationTicket,
    asIpaFile = false,
  }) {
    console.log('[DEBUG] installApp called with params:', { id, type, features, isFromRepo });
    const options = {
      enable_features: features,
      type,
      installation_ticket: installationTicket,
      as_ipa_file: Number(asIpaFile),
    };


    if (!isFromRepo) {
      options.id = id;
    } else {
      options.bundle_id = id;
      options.name = name;
      options.link = link;
      options.image = image;
      options.version = version;
    }

    // Keep track if we're in a cancelled state
    let operationCancelled = false;

    const doInstall = async (additionalOptions = {}) => {
      console.log('[DEBUG] doInstall executing with additionalOptions:', additionalOptions);

      // Don't continue if we've been cancelled
      if (operationCancelled) {
        console.log('[DEBUG] Installation was already cancelled, returning early');
        return null; // Return null instead of an object with success: false
      }

      const installOptions = { ...options, ...additionalOptions };

      // If this is from a cancellation, mark it to suppress error notifications
      if (additionalOptions._isCancelled) {
        installOptions._isCancelled = true;
      }

      console.log('[DEBUG] Sending install request with options:', installOptions);
      const resp = await this.post('install', installOptions, false);
      console.log('[DEBUG] install API response:', { success: resp.success, errors: resp.errors });

      if (!resp.success) {
        if (resp.errors && resp.errors[0] && resp.errors[0].code === 'ERROR_ALL_APPLE_BUNDLE_IDS_ARE_BUSY') {
          // If we got here after the user already saw the active apps dialog and clicked Continue,
          // it means the current set of options didn't resolve the issue. 
          // We should only block the modal from appearing again if there were actual
          // selections made (bundle_id specified or apps removed)

          // The previous logic was checking if additionalOptions had any keys, but
          // when a user clicks "Continue" without making any selections, additionalOptions is {},
          // which would prevent the modal from appearing again

          // Let's remove this check and allow the modal to appear again
          // This is normal behavior as Apple requires a specific app to be selected
          console.log('[DEBUG] Detected ERROR_ALL_APPLE_BUNDLE_IDS_ARE_BUSY');

          // Get active apps
          const activeApps = await this.call('get_active_apps');
          console.log('[DEBUG] Got active apps count:', activeApps?.length);

          if (!activeApps || !activeApps.length) {
            console.log('[DEBUG] No active apps, throwing APIError');
            throw new APIError(resp);
          }

          // Show popup with active apps list using the dedicated modal component
          return new Promise((resolve, reject) => {
            console.log('[DEBUG] Creating modal promise');
            let modalClosed = false;

            // Function to handle modal closing
            const handleCancelOrClose = () => {
              console.log('[DEBUG] handleCancelOrClose called');
              if (modalClosed) {
                console.log('[DEBUG] Modal already closed, ignoring');
                return; // Prevent multiple executions
              }
              modalClosed = true;
              operationCancelled = true; // Mark the entire operation as cancelled
              console.log('[DEBUG] Setting operationCancelled=true and resolving promise');

              // Simply resolve with null to indicate cancellation without triggering error flows
              resolve(null);
            };

            // Show the modal
            console.log('[DEBUG] Showing modal');
            Vue.prototype.$modal.show('active-apps-selection', {
              apps: activeApps,
              originalError: new APIError(resp),
              doInstall,
              resolve,
              handleCancel: handleCancelOrClose,
              reject
            });

            // Setup event listener for modal overlay clicks (outside modal)
            // Need to wait a bit for the modal to be fully rendered
            setTimeout(() => {
              console.log('[DEBUG] Setting up overlay click handler');
              const overlays = document.querySelectorAll('.vm--overlay');
              console.log('[DEBUG] Found overlays:', overlays?.length);
              if (overlays && overlays.length) {
                const modalOverlay = Array.from(overlays).find(
                  overlay => overlay.style.display !== 'none'
                );

                if (modalOverlay) {
                  console.log('[DEBUG] Found visible overlay');
                  const handleOutsideClick = (e) => {
                    console.log('[DEBUG] Overlay clicked, target:', e.target === modalOverlay ? 'overlay' : 'child');
                    // Only handle clicks directly on the overlay (not its children)
                    if (e.target === modalOverlay) {
                      handleCancelOrClose();
                      modalOverlay.removeEventListener('click', handleOutsideClick);
                    }
                  };

                  modalOverlay.addEventListener('click', handleOutsideClick);
                } else {
                  console.log('[DEBUG] No visible overlay found');
                }
              }
            }, 100);
          });
        }

        console.log('[DEBUG] API call failed but not with bundle error, throwing APIError');
        throw new APIError(resp);
      }

      console.log('[DEBUG] API call succeeded');
      Vue.store.dispatch('getConfig');
      return resp;
    };

    try {
      console.log('[DEBUG] Starting installation');
      const result = await doInstall();
      console.log('[DEBUG] Installation result:', result);

      // Check if the result is null, indicating cancellation
      if (result === null) {
        console.log('[DEBUG] Installation was cancelled by user, returning cancellation result');
        // Return a successful cancellation result instead of undefined
        // This ensures anyone working with the Promise gets a proper result
        return {
          success: true,
          cancelled: true,
          data: null
        };
      }

      return result;
    } catch (error) {
      // If the operation was cancelled, don't propagate the error
      if (operationCancelled) {
        console.log('[DEBUG] Error caught but operation was cancelled, returning cancellation result');
        return {
          success: true,
          cancelled: true,
          data: null
        };
      }

      console.error('[DEBUG] Installation error:', error);

      // For actual errors, let them propagate
      throw error;
    }
  }

