        <div class="language-selector">
          <button @click="langDropdownShown = !langDropdownShown" class="lang-selector-btn">
            {{ lang.name }}
            <span v-if="lang.translator" class="desc">({{ lang.translator }})</span>
          </button>
          <ul class="dropdown" v-show="langDropdownShown">
            <li v-for="(lang, key) in langs" :key="key" @click="switchLang(key)">
              {{ lang.name }}
              <span v-if="lang.translator" class="desc">({{ lang.translator }})</span>
            </li>
