<script>
import { mapState } from 'vuex';

export default {
  data() {
    return {
      langDropdownShown: false,
      langs: [],
    };
  },
  computed: {
    lang() {
      return this.langs[this.$store.getters.lang] || {};
    },
    ...mapState(['theme']),
    BRAND_CONFIG() {
      return this.$store.getters.brandConfig;
    },
    BRAND_KEY() {
      return this.$store.getters.currentBrand;
    }
  },
  methods: {
    getLangs() {
      return this.$api.getLanguages().then((data) => {
        this.langs = data;
      });
    },
    switchLang(lang) {
      this.$store.dispatch('setLang', lang);
      this.langDropdownShown = false;
    },
  },
  serverPrefetch() {
    return this.getLangs();
  },
  mounted() {
    this.getLangs();

    document.body.addEventListener('click', (e) => {
      if (!e.target.closest('.lang-selector-btn')) {
        this.langDropdownShown = false;
      }
    });
  },
};
</script>

