    }

    &.replace:hover {
        background-color: var(--success-hover-bg, #c8e6c9);
    }
}

.modal-footer {
    display: flex;
    justify-content: flex-end;
    padding-top: 15px;
    margin-top: 15px;
    border-top: 1px solid var(--tertiary-accent);

    button:last-child {
        margin-left: 10px;
    }
}
</style>