<template>
  <vue-markdown class="markdown" :html="false" :source="content" :breaks="true" :linkify="true" :anchor-attributes="{
    rel: 'noopener noreferrer'
  }" />
</template>

<script>
import VueMarkdown from 'vue-markdown-v2';

export default {
  props: {
    content: String,
  },
  components: {
    VueMarkdown,
  },
  methods: {
    isInternalLink(href) {
      if (!href) return false;

      // Check if it's a relative URL (starts with '/' and isn't an external link)
      if (href.startsWith('/') && !href.match(/^(https?:|mailto:|tel:|ftp:)/)) {
        return true;
      }

      // Check if URL matches current hostname
      try {
        const url = new URL(href);
        return url.hostname === window.location.hostname;
      } catch (e) {
        // If URL parsing fails, it's likely not a valid URL
        return false;
      }
    },
    setupInternalLinks() {
      // Get all links in the markdown content
      const links = this.$el.querySelectorAll('a');

      // Process each link
      links.forEach(link => {
        const href = link.getAttribute('href');

        if (this.isInternalLink(href)) {
          // For internal links, use Vue Router
          link.removeAttribute('target'); // Remove target="_blank" for internal links
          link.addEventListener('click', (event) => {
            event.preventDefault();
            // For absolute URLs matching our hostname, convert to path only
            let path = href;
            try {
              const url = new URL(href);
              if (url.hostname === window.location.hostname) {
                path = url.pathname + url.search + url.hash;
              }
            } catch (e) {
              // If URL parsing fails, keep the original href
            }
            this.$router.push(path);
          });
        }
      });
    }
  },
  mounted() {
    // Setup internal links after the component is rendered
    this.$nextTick(() => {
      this.setupInternalLinks();
    });
  }
};
</script>

